<template>
	<view class="flex flex-direction bg-white" style="height: 100vh;">
		<!-- 是否登录 -->
		<is-login currentPage="index"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">产品目录</block>
		</cu-custom>

		<view v-for="(item , i) in tabber_list" :key="i">
			<!-- 标题 -->
			<view v-if="item.iconsList.length > 0 && item.title_show" class="cu-bar bg-white solid-top ">
				<view class="action">
					<text class="cuIcon-title " :class="'text-'+item.color"></text> {{ item.title}}
				</view>
			</view>
			<!-- 宫格 -->
			<view v-if="item.iconsList.length > 0" class="cu-list grid no-border "
				:class="'col-'+ item.colNumber + ' ' + item.bgColor" :data-cur="item.cur">
				<!-- 总宫格数小于等于item.colNumber*2 || 总宫格大于item.colNumber*2 ， index < item.colNumber*2 -1-->
				<view class="cu-item " v-for="(icon,index) in item.iconsList" :key="index"
					v-if="(item.iconsList.length <= item.colNumber*2) || (item.iconsList.length > item.colNumber*2 && index < item.colNumber*2 -1)"
					@tap="navigateTo" :data-url="icon.url" :data-exec="icon.exec">
					<view :class="['cuIcon-' + icon.cuIcon,'text-' + icon.color]">
					</view>
					<text>{{icon.name}}</text>
				</view>

				<!-- 总宫格大于item.colNumber*2 ，index > item.colNumber*2-1 -->
				<view class="cu-item " v-for="(icon,index) in item.iconsList" :key="index"
					v-if="item.iconsList.length > item.colNumber*2 && index >= item.colNumber*2 -1 && item.showAll"
					@tap="navigateTo" :data-url="icon.url">
					<view :class="['cuIcon-' + icon.cuIcon,'text-' + icon.color]">
					</view>
					<text>{{icon.name}}</text>
				</view>
				<!-- 全部或折叠按钮 -->
				<view v-if="item.iconsList.length >item.colNumber*2" class="cu-item" :data-cur="item.cur"
					@click="showAllGrid">
					<view class=" text-blue" :class="item.showAll ? 'cuIcon-fold' : 'cuIcon-apps'">
					</view>
					<text>{{ item.showAll ? '折叠' :'全部' }}</text>
				</view>
			</view>
		</view>

		<navigator url="/pages/index/index" class="flex flex-direction">
			<button class="bg-cyan cu-btn padding round margin">首页</button>
		</navigator>
	</view>
</template>

<script>
	/**
	 * 产品目录
	 * @陆彦捷
	 * @date 2021.06.23 首发
	 * @date 2021.07.13 产品目录(改为宫格样式)
	 * @description 链接产品分类、产品型号、设备按钮
	 */
	export default {
		data() {
			return {
				// 布局样式
				preview: false, // 是否返回上一页
				tabber_list: [{
					title: '产品目录',
					cur: 'goods',
					bgColor: '', // 背景颜色
					color: 'blue',
					title_show: false,
					showAll: false,
					colNumber: 5, // 显示的列数
					iconsList: [{
							cuIcon: 'list',
							color: 'yellow',
							name: '产品分类',
							url: '/pages/basic/product/producttype/home?preview=true'
						},
						{
							cuIcon: 'goodsnew',
							color: 'blue',
							name: '产品型号',
							url: '/pages/basic/product/productmodel/home?preview=true'
						},
						{
							cuIcon: 'shake',
							color: 'olive',
							name: '设备',
							url: '/pages/basic/product/product/home?preview=true'
						},
						{
							cuIcon: 'questionfill',
							color: 'yellow',
							name: '常见问题',
							url: '/pages/basic/product/problem/home?preview=true'
						}
					]
				}, ], //  列表显示项
			}
		},
		onLoad: function(options) {
			this.getParams(options);
		},
		methods: {
			//=========================================== 初始化方法 =======================================
			/** 初始化参数
			 * @param {Object} options 初始化参数
			 */
			getParams: function(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false;

			},
			// =============================================================================================
		}
	}
</script>

<style>
	.nav-list {
		display: flex;
		flex-wrap: wrap;
		padding: 0px 40upx 0px;
		justify-content: space-between;
	}

	.nav-li {
		padding: 30upx;
		border-radius: 12upx;
		width: 45%;
		margin: 0 2.5% 40upx;
		background-image: url(https://cdn.nlark.com/yuque/0/2019/png/280374/1552996358352-assets/web-upload/cc3b1807-c684-4b83-8f80-80e5b8a6b975.png);
		background-size: cover;
		background-position: center;
		position: relative;
		z-index: 1;
	}

	.nav-li::after {
		content: "";
		position: absolute;
		z-index: -1;
		background-color: inherit;
		width: 100%;
		height: 100%;
		left: 0;
		bottom: -10%;
		border-radius: 10upx;
		opacity: 0.2;
		transform: scale(0.9, 0.9);
	}

	.nav-li.cur {
		color: #fff;
		background: rgb(94, 185, 94);
		box-shadow: 4upx 4upx 6upx rgba(94, 185, 94, 0.4);
	}

	.nav-title {
		font-size: 32upx;
		font-weight: 300;
	}

	.nav-title::first-letter {
		font-size: 40upx;
		margin-right: 4upx;
	}

	.nav-name {
		font-size: 28upx;
		text-transform: Capitalize;
		margin-top: 20upx;
		position: relative;
	}

	.nav-name::before {
		content: "";
		position: absolute;
		display: block;
		width: 40upx;
		height: 6upx;
		background: #fff;
		bottom: 0;
		right: 0;
		opacity: 0.5;
	}

	.nav-name::after {
		content: "";
		position: absolute;
		display: block;
		width: 100upx;
		height: 1px;
		background: #fff;
		bottom: 0;
		right: 40upx;
		opacity: 0.3;
	}

	.nav-name::first-letter {
		font-weight: bold;
		font-size: 36upx;
		margin-right: 1px;
	}

	.nav-li text {
		position: absolute;
		right: 30upx;
		top: 30upx;
		font-size: 52upx;
		width: 60upx;
		height: 60upx;
		text-align: center;
		line-height: 60upx;
	}

	.text-light {
		font-weight: 300;
	}

	@keyframes show {
		0% {
			transform: translateY(-50px);
		}

		60% {
			transform: translateY(40upx);
		}

		100% {
			transform: translateY(0px);
		}
	}

	@-webkit-keyframes show {
		0% {
			transform: translateY(-50px);
		}

		60% {
			transform: translateY(40upx);
		}

		100% {
			transform: translateY(0px);
		}
	}
</style>
